.rental-title {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 36px;
}

.rental-prices-summer {
  border-collapse: collapse;
  background: #fff;
  border-style: hidden;
  border-radius: 30px;
  margin-bottom: 60px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 0 0 2px #f6af04;
}

.rental-prices-summer tr th, .rental-prices-summer tr td {
  text-transform: uppercase;
  border: 2px solid #f6af04;
  padding: 15px;
  font-size: 20px;
  font-weight: bold;
  line-height: 24px;
}

.rental-prices-summer.winter tr th, .rental-prices-summer.winter tr td {
  border: 2px solid var(--accent-color);
}

.rental-prices-summer.winter {
  box-shadow: 0 0 0 2px var(--accent-color);
}

@media screen and (max-width: 1199.98px) {
  .rental-title {
    font-size: 24px;
  }

  .rental-prices-summer tr th, .rental-prices-summer tr td {
    font-size: 16px;
    line-height: 1.2;
  }
}

@media screen and (max-width: 767.98px) {
  .rental-title {
    font-size: 18px;
  }

  .rental-prices-summer tr th, .rental-prices-summer tr td {
    padding: 4px;
    font-size: 10px;
  }
}

